.Container{
    background-color: #1c73b6;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 1rem;
    text-align: center;
    padding-bottom: 5rem;
}
h2, p{
    margin-block-start: 0;
    margin-block-end: 0;
}
.Header{
    margin-top: 2rem;
    padding-top: 2rem;
    background-color: #1c73b6;
}
.Header h2{
    margin-top: 0;
    font-size: 2rem;
    color: black;
}
.Header h4{
    margin-block-start: 0;
    margin-block-end: 0;
    font-size: 2rem;
    margin-top: 5rem;
}

.TextDiv{
    font-size: 1.2rem;
}

.ImageDiv > div:hover{
    transform: scale(1);
}

.ImageDiv img{
        max-width: 100%;
}

.Paragraph{
    margin: 3rem 0;
}

.Paragraph p{
    text-align: center;
}

.White{
    background-color: white;
}

@media (min-width: 576px) {
    .Container{
        flex-direction: row;
    }
    .TextDiv{
        margin-right: 5rem;
        text-align: left;
    }
    .ImageDiv img {
        width: 590px;
    }
}